<!-- 日志管理 -->
<template>
  <section class="info">
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="采集数据查询" name="first">
        <el-col :span="24" class="">
          <el-form :inline="true">
            <el-col :span="5">
              <el-form-item>
                <span>设备厂家：</span>
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item>
                <span>设备型号：</span>
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item>
                <span>序列号：</span>
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <span></span>
                <el-date-picker
                  v-model="value1"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                >
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item>
                <el-button type="primary" v-on:click="getUsers">查询</el-button>
              </el-form-item>
            </el-col>
          </el-form>
        </el-col>
        <el-table
          :data="users"
          highlight-current-row
          v-loading="listLoading"
          style="width: 100%;"
        >
          <el-table-column type="index" min-width="15%"> </el-table-column>
          <el-table-column prop="param1" label="厂家" min-width="10%">
          </el-table-column>
          <el-table-column prop="param2" label="设备型号" min-width="10%">
          </el-table-column>
          <el-table-column prop="param3" label="注册日期" min-width="10%">
          </el-table-column>
          <el-table-column prop="param4" label="设备类型" min-width="10%">
          </el-table-column>
          <el-table-column prop="param5" label="序列号" min-width="10%">
          </el-table-column>
          <el-table-column prop="param6" label="安装位置" min-width="10%">
          </el-table-column>
          <el-table-column prop="param7" label="机构" min-width="10%">
          </el-table-column>
          <el-table-column prop="param8" label="IP" min-width="10%">
          </el-table-column>
          <el-table-column prop="param9" label="GIS" min-width="10%" sortable>
          </el-table-column>
          <el-table-column label="操作" min-width="20%">
            <template slot-scope="scope">
              <el-button size="small" @click="handleEdit(scope.$index, scope.row)">配置</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-col :span="24" class="pagination">
          <el-pagination
            layout="prev, pager, next"
            @current-change="handleCurrentChange"
            :page-size="10"
            :total="total"
            style="float:right;"
            background
          >
          </el-pagination>
        </el-col>
      </el-tab-pane>
      <el-tab-pane label="定期数据" name="second">
        <el-row>
          <el-col :span="24" class="">
            <div id="chartLine" style="width:100%; height:400px;"></div>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </section>
</template>

<script>
//导入其他文件比如：组件/工具js/第三方插件js/json文件/图片等等import 《组件名称》 from '《组件路径》';
import util from "@/common/js/util";
import {
  getUserListPage,
  removeUser,
  batchRemoveUser,
  editUser,
  addUser,
  getDeviceLists
} from "@/api/api";
import echarts from "echarts";

export default {
  //import引入的组件需要注入到对象中才能使用
  name: "connectInfo",
  props: {},
  components: {},
  data() {
    return {
      activeName: "second",
      value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      options: [
        {
          value: "选项1",
          label: "数据库"
        },
        {
          value: "选项2",
          label: "文件"
        },
        {
          value: "webservice",
          label: "错误(error)"
        },
        {
          value: "选项4",
          label: "数据流"
        }
      ],
      value: "",
      value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      listLoading: false,
      filters: {
        name: ""
      },
      users: [],
      total: 0,
      page: 1,
      chartLine: null,
    };
  },
  beforeCreate() {
    //生命周期 - 创建之前
  },
  created() {},
  beforeMount() {}, //生命周期 - 挂载之前
  mounted() {
    this.getUsers();

    this.drawLineChart();
  },
  beforeUpdate() {}, //生命周期 - 更新之前
  computed: {
    //监听属性 类似于data概念
  },
  //方法集合
  methods: {
    handleClick(tab, event) {
      console.log(tab, event, this.activeName);
      if (this.activeName === 'second') this.drawLineChart();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getUsers();
    },
    //获取用户列表
    getUsers() {
      let para = {
        page: this.page,
        name: this.filters.name
      };
      this.listLoading = true;
      //NProgress.start();getUserListPage
      getDeviceLists(para).then(res => {
        this.total = res.data.total;
        this.users = res.data.users;
        this.listLoading = false;
        //NProgress.done();
      });
    },
    handleEdit() {

    },
    drawLineChart() {
      this.chartLine = echarts.init(document.getElementById("chartLine"));
      this.chartLine.setOption({
        title: {
          text: "Line Chart"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["测试数据"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["10.1", "10.2", "10.3", "10.4", "10.5", "10.6", "10.7", "10.8", "10.9", "10.10"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "测试数据",
            type: "line",
            stack: "总量",
            data: [120, 132, 101, 134, 90, 230, 210, 90, 230, 210]
          }
        ]
      });
    },
  },
  watch: {
    //监控data中的数据变化
  },
  updated() {
    if (this.activeName === 'second') this.drawLineChart();
  }, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>

<style lang="scss" scoped>
//@import url(); 引入公共css类
.info {
  .pagination {
    margin-top: 20px;
  }
}
</style>
